.profile {
  padding: 24px;
}

@media (min-width: 992px) {
  .profile {
    padding: 40px;
  }
}

.content {
  display: grid;
  grid-gap: 40px;
  grid-template:
    "section1" auto
    "section2" auto
    "section3 " auto / 100%;
  justify-content: center;
}

@media (min-width: 768px) {
  .content {
    grid-template:
      "section1 section2" auto
      "section1 section3" auto / minmax(0, 312px) minmax(0, 312px);
  }
}

@media (min-width: 1200px) {
  .content {
    grid-template:
      "section1 section2 section3" auto / minmax(0, 312px) minmax(0, 312px)
      minmax(0, 312px);
  }
}

.section {
  & > *:not(:first-child) {
    margin-top: 24px;
  }
  &:nth-child(1) {
    grid-area: section1;
  }
  &:nth-child(2) {
    grid-area: section2;
  }
  &:nth-child(3) {
    grid-area: section3;
  }
}

.title {
  margin-bottom: 24px;
  font-size: 24px;
  text-transform: uppercase;
}

.sectionTitle {
  font-size: 18px;
}

.profileIcon {
  margin: auto;
  display: block;
  width: 80px;
}

.userName {
  text-align: center;
  font-size: 20px;
}

.email {
  text-align: center;
  font-size: 15px;
}

.button {
  display: block;
  width: 100%;
  height: 40px;
  font-size: 16px;
  color: #f7f7f7;
  background-color: #41b167;
  border-radius: 4px;
}

.text {
  font-size: 14px;
}

.label {
  margin-bottom: 4px;
  font-size: 12px;
}

.divider {
  border-top: 1px solid #707070;
  opacity: 0.5;
}
